import React from 'react';
import logo from './logo.png';
import './Logo.css';

const Logo = () => {
    return (
        <a href="https://hemant-garg.github.io/youtube-mini"><img src={logo} alt='youtube mini' className='logo'/></a>
    );
}

export default Logo;


